@use "global" as *;

.header {
  height: rem(100);
  width: 100%;
  letter-spacing: 0.64px;
  background: $white;

  @include mq("md") {
    height: rem(80);
  }
}

.header__inner {
  position: relative;
  z-index: $layer-header + 5;
  padding-inline: rem(64) rem(20);
  max-width: rem(1440);
  width: 100%;
  margin-inline: auto;
  height: inherit;
  display: flex;
  justify-content: space-between;

  @include mq(inner) {
    padding-inline: rem(30) rem(15);
  }

  @include mq(md) {
    padding-inline: rem(20) rem(30);
  }
}

.header__logo {
  font-size: 0;
  height: inherit;
  max-width: rem(220);
  width: 100%;
  flex-shrink: 0;

  @media (max-width: 1260px) {
    max-width: rem(180);
  }

  @include mq(inner) {}


  @include mq(md) {
    // max-width: rem(160);
  }
}

.header__logo a {
  display: flex;
  align-items: center;
  flex-direction: column;
  // justify-content: center;
  height: inherit;
  padding-top: rem(18);

}

.header__logo img {
  // max-width: rem(211);
  width: 100%;
  height: auto;
  object-fit: contain;

  @include mq("md") {}
}

// .header__logo span {
//   font-size: 10px;
//   letter-spacing: 0.5px;
//   white-space: nowrap;
// }

.header__nav-lists {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
}

.header__nav {
  height: inherit;

  @include mq("md") {
    display: none;
    height: inherit;
  }
}

.header__nav-items {
  display: flex;
  justify-content: flex-end;
  height: inherit;
}

.header__nav-item {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;

}

.header__nav-link {
  cursor: pointer;
  padding: rem(10) rem(10);
  height: inherit;
  display: flex;
  align-items: center;
  white-space: nowrap;
  transition: color 0.3s ease 0s;

  @include mq(inner) {
    padding: rem(8);
  }
}

.header__nav-link span {
  max-width: fit-content;
  padding-bottom: rem(6);
  position: relative;

  &::after {
    content: "";
    position: absolute;
    background: $gray;
    width: 0;
    height: rem(1);
    bottom: 0;
    left: 0;
    transition: width 0.2s ease-out 0s;
  }
}

/* current */
.header__nav-link.current.header__nav-link span {
  &::after {
    width: 100%;
  }
}

