.header__nav-contact {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: rem(10);

  @media (max-width: 1260px) {
    margin-left: 0;

  }

  @include mq(inner) {}

  @include mq(md) {}
}

.header__nav-contact-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;

  @include mq(md) {
    flex-direction: column;
  }
}


.header__nav-contact-item:last-child {
  margin-left: rem(20);


  @media (max-width: 1260px) {
    margin-left: rem(15);
  }

  @include mq(inner) {
    // margin-left: rem(15);
  }

  @include mq(md) {
    margin-left: 0;
    margin-top: rem(20);
  }
}

.header__nav-contact-item a {
  display: block;
  line-height: 1;
  width: rem(180);
  height: rem(80);
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-out;

  @media (max-width: 1260px) {
    width: rem(170);
  }

  @include mq(inner) {
    // width: rem(170);
  }

  @include mq(md) {
    margin-inline: auto;
    width: rem(200);
  }
}

.header__nav-contact-item--blue a {
  color: $white;
  background-color: $main-color;
  border: 1px solid $main-color;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.header__nav-contact-item--blue p {
  letter-spacing: 0.64px;
}

.header__nav-contact-item--blue span {
  font-size: rem(12);
  letter-spacing: 0.48px;
  padding-top: rem(3);
}

.header__nav-contact-item--white a {
  color: $text;
  background-color: $white;
  border: 1px solid $text;
}

@media (hover: hover) {
  .header__nav-contact-item--blue a:hover {
    color: $main-color;
    background-color: $white;
  }

  .header__nav-contact-item--white a:hover {
    color: $white;
    background-color: $text;
  }
}

