/* hamburger */
.header__hamburger {
  display: none;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  position: relative;
  z-index: 999;
  width: rem(24);
  height: rem(16);
  cursor: pointer;
  transition: .3s;
  right: 0;

  @include mq("md") {
    display: block;
  }
}

.header__hamburger.is-open {
  background-color: transparent;
}

.header__hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: rem(24);
  height: 1px;
  background-color: $main-color;
  transition: .5s;
}

.header__hamburger span:nth-of-type(1) {
  top: -2px;
}

.header__hamburger span:nth-of-type(2) {
  top: 0;
}

.header__hamburger span:nth-of-type(3) {
  top: 2px;
}

.header__hamburger.is-open span:nth-of-type(1) {
  top: 0;
  transform: translateX(-50%) rotate(45deg);
}

.header__hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.header__hamburger.is-open span:nth-of-type(3) {
  top: -2px;
  transform: translateX(-50%) rotate(-45deg);
}


/* drawer */
.header__drawer {
  position: fixed;
  z-index: $layer-header + 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
  color: $text;
  transition: transform 0.5s ease;
  transform: translateX(100%);
  font-size: rem(12);
  letter-spacing: 0.11em;
  font-weight: 500;
  margin-top: rem(80);
}

.header__drawer-bg {
  background: rgba($main-color, 0.9);
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: $layer-header + 2;
}

.header__drawer-nav {
  position: relative;
  z-index: $layer-header + 3;
  background: $white;
  margin: auto;
  padding-block: rem(40) rem(60);
}

.header__drawer.is-open {
  transform: translateX(0);
}

.header__drawer::-webkit-scrollbar {
  display: none;
}

.header__drawer-items {
  text-align: center;
  padding-left: rem(15);
}

.header__drawer-item--mt24 {
  margin-top: rem(24);
}

.header__drawer-menu-link {
  padding: .4em;
}

.header__drawer-menu-link span {
  display: block;
  width: 100%;
}



.header__drawer-link {
  padding: rem(18) 0;
  display: block;
}

/* spコンタクトボタン */

.header__drawer-contact-items {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include mq(md) {
    flex-direction: column;
  }
}


.header__drawer-contact-item:last-child {
  margin-top: rem(20);
}

.header__drawer-contact-item a {
  display: block;
  line-height: 1;
  height: rem(80);
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-out;
  margin-inline: auto;
  width: rem(200);

  @include mq(md) {}
}

.header__drawer-contact-item--blue a {
  color: $white;
  background-color: $main-color;
  border: 1px solid $main-color;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.header__drawer-contact-item--blue p {
  letter-spacing: 0.64px;
}

.header__drawer-contact-item--blue span {
  font-size: rem(12);
  letter-spacing: 0.48px;
  padding-top: rem(3);
}


.header__drawer-contact-item--white a {
  color: $text;
  background-color: $white;
  border: 1px solid $text;
}

/* current */
.header__drawer-link.current {
  color: $main-color;
}